.forecast {
    width: 100%;
    flex-wrap: wrap;
    padding: 0 2.5vw;
    justify-content: space-between;
    margin: 5vh 0;
    display: flex;
}

.user-coords {
    text-align: center;
    text-transform: uppercase;
    font-size: 50px;
    color: #fff;
    text-shadow: 1px 1px 2px #000;
    margin: 5% 0;
    font-weight: 300;
}

.user-coords span {
    font-weight: bold;
}

.day {
    /*liczba dni tygodnia*/
    flex-basis: calc(90%/7);
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    transition: .4s ease;
    padding: 5px 5px 10px;
    text-align: center;
}

.hide {
    display: none;
    opacity: 0;
    transition: .4s ease;
}

.day h4 {
    margin: 10px 0;
    font-size: 20px;
}

.day p {
    margin: 1rem 0;
}

.day span {
    margin: 0 10px;
}

.day:hover {
    background-color: rgba(0, 0, 0, 1);
}

.day:hover i {
    font-weight: bold;
}

.day .description {
    margin: 2vh 0 0.75vh;
    font-size: 16px;
}