* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

body {
    position: relative;
    color: white;
    scroll-behavior: smooth;
    background: url(../img/img.png);
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    width: 100%;
    min-height: 100vh;
}

html {
    overflow-y: scroll;
}

*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background-image: linear-gradient(#366, #111);
}

*::-webkit-scrollbar-thumb {
    background-image: linear-gradient(#699, #366);
    border-radius: 20px;
}

h1 {
    font-weight: 300;
    font-size: 1.6rem;
}